<template>
  <p class="blank"></p>
  <div class="container">
    <div class="row">
      <div class="col-lg-2" v-for="i in lists.slice(1,13)" :key="i.id">
        <div class="thumbnail">
          <img :src="this.url + i.cover" alt=""/>
          <div class="caption">
            <router-link :to="{ name: 'details', params: { id: i.id } }"><h4>{{ i.name }}</h4></router-link>
            <h5>{{ i.playDate }}</h5>
            <div class="text-danger text-right">
              <span>
                评分：{{ i.score }}
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
      return {
        lists: [],
        url:"http://124.93.196.45:10001/"
      };
  },
  created() {
      this.axios({
        method:"get",
        url:"/prod-api/api/movie/film/list"
      })
      .then((response) => {
          if (response.status === 200) {
            this.lists = response.data.rows;
          }
      })
      .catch((error) => console.log(error));
  },
}
</script>

<style scoped>

.thumbnail img{
  height: 220px;
}
</style>